.gpt3__blog{
    display: flex;
    flex-direction: column;
}

.gpt3__blog-heading{
    width: 100%;
    text-align: left;

    margin-bottom: 5rem;
}

.gpt3__blog-heading h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04rem;
}

.gpt3__blog-container{
    display: flex;
}

.gpt3__blog-container-groupA{
    flex: 0.75;
    margin-right: 2rem;
}

.gpt3__blog-container-groupB{
    flex: 1;

    display: grid;
    grid-template-columns: repeat(2 ,1fr);
    grid-gap: 2rem; 
}
.gpt3__blog-container-groupB span{
   flex: 1;
   height: 50%;
   /* padding:  1rem; */
}
/* .gpt3__blog-container-groupB> span> span{
    margin:  3rem;
} */
.gpt3__blog-container-groupB .gpt3__blog-container-article-content h3{
    font-size: 20px;
    line-height: 28px;
}


@media screen and (max-width: 990px) {
    .gpt3__blog-container{
        flex-direction: column-reverse;
    }
    .gpt3__blog-container-groupA{
        margin: 2rem 0;
    }
}

@media screen and (max-width: 700px){
    .gpt3__blog-container-groupB{
        grid-template-columns: repeat(1, 1fr);
    }
    .gpt3__blog-heading h1{
        font-size: 46px;
        line-height: 60px;
    }
}

@media screen and (max-width: 550px){
    .gpt3__blog-heading h1{
        font-size: 36px;
        line-height: 46px;
    }
}