.gpt3__possibililty{
    display: flex;
    flex-direction: row;
}
.gpt3__possibililty-image{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    margin: 1rem 3rem;
}
.gpt3__possibililty-image img{
    width: 100%;
    height: 100%;
}

.gpt3__possibililty-content{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
}

.gpt3__possibililty-content h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    margin-bottom: 1rem; 
}
.gpt3__possibililty-content h4{
    font-family: var(--font-family);
    font-size: 16px;
    line-height: 30px;
    font-weight: 500;
    color: #71E5EF;
    margin: 1rem 0;
    cursor: pointer;
}
.gpt3__possibililty-content h4:nth-of-type(2){
    color: var(--color-subtext);
}
.gpt3__possibililty-content p{
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-text);
    margin-bottom: 2rem;
}

@media screen and (max-width: 950px) {
    .gpt3__possibililty{
        flex-direction: column;
    }
    .gpt3__possibililty-image {
        margin: 1rem 0;
    }
    .gpt3__possibililty-content{
        margin: 2rem 0;
    }
}