.gpt3__whatgpt3{
    display: flex;
    flex-direction: column;
    padding: 2rem;
    background: var(--color-footer);

    
    background:-moz-linear-gradient(160deg, rgba(4, 44, 84, 1) 0%, rgba(3, 27, 52, 1) 48%, rgba(4, 44, 84, 1) 100%, rgba(4, 44, 84, 1) 101%); 

    /* safari 5.1+,chrome 10+ */
    background:-webkit-linear-gradient(160deg, rgba(4, 44, 84, 1) 0%, rgba(3, 27, 52, 1) 48%, rgba(4, 44, 84, 1) 100%, rgba(4, 44, 84, 1) 101%);

    /* opera 11.10+ */
    background:-o-linear-gradient(160deg, rgba(4, 44, 84, 1) 0%, rgba(3, 27, 52, 1) 48%, rgba(4, 44, 84, 1) 100%, rgba(4, 44, 84, 1) 101%);

    /* ie 6-9 */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#042C54', endColorstr='#042C54', GradientType=1 );

    /* ie 10+ */
    background:-ms-linear-gradient(160deg, rgba(4, 44, 84, 1) 0%, rgba(3, 27, 52, 1) 48%, rgba(4, 44, 84, 1) 100%, rgba(4, 44, 84, 1) 101%);

    /* global 94%+ browsers support */
    background:linear-gradient(160deg, rgba(4, 44, 84, 1) 0%, rgba(3, 27, 52, 1) 48%, rgba(4, 44, 84, 1) 100%, rgba(4, 44, 84, 1) 101%);
}

.gpt3__whatgpt3-feature{
    display: flex;
}

.gpt3__whatgpt3-feature .gpt3__whatgpt3-container-feature{
    margin: 0;
}

.gpt3__whatgpt3-feature .gpt3__whatgpt3-container-feature-text{
    max-width: 700px;
}

.gpt3__whatgpt3-heading{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    margin: 4rem 0 2rem;
}

.gpt3__whatgpt3-heading h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    max-width: 510px;
    line-height: 45px;
}

.gpt3__whatgpt3-heading p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;

    color: var(--color-subtext);
    cursor: pointer;
}

.gpt3__whatgpt3-container{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin-top: 2rem;
}

.gpt3__whatgpt3-container .gpt3__features-container-feature{
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

.gpt3__whatgpt3-feature .gpt3__features-container-feature-text{
    max-width: 900px;
    margin-right: 3rem;
}


@media screen and (max-width: 850px) {
    .gpt3__whatgpt3-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .gpt3__whatgpt3-heading p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {
    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-feature .gpt3__features-container-feature {
        flex-direction: column;
    }

    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-feature .gpt3__features-container-feature-text {
        margin-top: 0.5rem;
    }

    .gpt3__whatgpt3-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-container .gpt3__features-container-feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}
